body {
  margin: 0;
  padding: 0;
}

.app-header {
  width: 100%;
  height: 100vh;
  background: url("./resources/background1.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}
.header-container {
  width: 80%;
  height: 83px;
  display: flex;
  align-items:center;
  justify-content: space-between;
  color: #ffff;
  padding-left: 120px;
}
.header-container > button {
  background: none;
  border: none;
  color: white;
  font-size: 18px;
}
.header-container::after{
  content: "";
  background-color: #ffff;
  height: 0.01em;
  position: absolute;
  top: 10vh;
  left: 0vh;
  width: 100%;
}
.logo {
  scale: 80%;
  margin-right: 0;
}
.link-container {
  display: flex;
  margin: 0;
  justify-content: space-around;
  align-items: center;
  width: 500px;
  padding-left: 50px;
  padding-right: 50px;
}
.link-container > a {
  color: #ffff;
  text-decoration: none;
  position: relative;
  font-size: 20px;
}
.link-container > a:hover {
  color: red;
}
.link-container > a::after {
  content: "";
  background-color: red;
  width: 78px;
  height: 4px;
  position: absolute;
  bottom: -2.5vh;
  left: 1px;
  visibility: hidden;
}
